body {
	color: var(--color-text-main, red);
	padding-top: 0;
}

.fas, .far {
	padding-right: 10px;
}

.site {
	@at-root #{&}-content {
		max-width: 60rem;
		margin: 0 auto;
	}
}

body.login {
	height: 100vh;
	overflow: hidden;
	padding-top: 0;
	display: flex;

	.site {
		flex: 1 0 auto;
		background: #EEE;
		border: 4rem solid #FFF;
		display: flex;
		align-items: center;
		justify-content: space-between;

		#{&}-header {
			display: none;
		}
	}
}

.green {
	color: #607D89;
}

.center {
	text-align: center;
}

.experience {
	background: white;
	border-radius: 2em;
	padding: .3em;
	opacity: 1;

	h2 {
		margin-top: 3rem;
	}
}